<script>
export default {
  name: "inputMemberBG",
  data() {
    return {
      sum: sessionStorage.getItem("currentSum"),

      // 一般数组
      member: [],
      gender: [],

      // json数组
      members: [],

      boys: [],
      girls: [],

      // 天使
      boysAngel: [],
      girlsAngel: []
    }
  },
  methods: {
    dealBG() {
      // 合并两个数组,成为一个json数组
      for (let i = 0; i < this.sum; i++) {
        this.members[i] = {};
        this.members[i].name = this.member[i];
        this.members[i].gender = this.gender[i];
      }
      // 排序,男女拆成两个数组
      let boysCount = 0;
      let girlsCount = 0;
      for (let i = 0; i < this.sum; i++) {
        if (this.members[i].gender == "男") {
          this.boys[boysCount] = this.members[i].name;
          boysCount++;
        } else {
          this.girls[girlsCount] = this.members[i].name;
          girlsCount++;
        }
      }
      // 先把多的打乱,分给少的
      if (girlsCount >= boysCount) {
        // 把girls打乱
        let girlsDealed = JSON.parse(JSON.stringify(this.girls));
        for (let i = girlsCount - 1; i > 0; i--) {
          let r = Math.floor(Math.random() * i);
          let t = girlsDealed[i];
          girlsDealed[i] = girlsDealed[r];
          girlsDealed[r] = t;
        }

        // 给男生分主人
        for (let i = 0; i < boysCount; i++) {
          this.boysAngel[i] = girlsDealed[i];
        }

        // 把girlsDeals里剩下的人和boys拼在一起
        let rest = [];
        let restIndex = 0;
        for (let i = boysCount; i < girlsCount; i++, restIndex++) {
          rest[restIndex] = girlsDealed[i];
        }
        for (let i = 0; i < boysCount; i++, restIndex++) {
          rest[restIndex] = this.boys[i];
        }

        // 把rest打乱
        for (let i = girlsCount - 1; i > 0; i--) {
          let r = Math.floor(Math.random() * i);
          let t = rest[i];
          rest[i] = rest[r];
          rest[r] = t;
        }

        // 给女生分主人
        for (let i = 0; i < girlsCount; i++) {
          this.girlsAngel[i] = rest[i];
        }
      } else {
        let boysDealed = JSON.parse(JSON.stringify(this.boys));
        for (let i = boysCount - 1; i > 0; i--) {
          let r = Math.floor(Math.random() * i);
          let t = boysDealed[i];
          boysDealed[i] = boysDealed[r];
          boysDealed[r] = t;
        }
        for (let i = 0; i < girlsCount; i++) {
          this.girlsAngel[i] = boysDealed[i];
        }
        let rest = [];
        let restIndex = 0;
        for (let i = girlsCount; i < boysCount; i++, restIndex++) {
          rest[restIndex] = boysDealed[i];
        }
        for (let i = 0; i < girlsCount; i++, restIndex++) {
          rest[restIndex] = this.girls[i];
        }
        for (let i = boysCount - 1; i > 0; i--) {
          let r = Math.floor(Math.random() * i);
          let t = rest[i];
          rest[i] = rest[r];
          rest[r] = t;
        }
        for (let i = 0; i < boysCount; i++) {
          this.boysAngel[i] = rest[i];
        }
      }

      console.log(this.boys);
      console.log(this.boysAngel);
      console.log(this.girls);
      console.log(this.girlsAngel);

      sessionStorage.setItem("boys", JSON.stringify(this.boys));
      sessionStorage.setItem("boysAngel", JSON.stringify(this.boysAngel));
      sessionStorage.setItem("girls", JSON.stringify(this.girls));
      sessionStorage.setItem("girlsAngel", JSON.stringify(this.girlsAngel));
      sessionStorage.setItem("currentBoy", JSON.stringify(boysCount));
      sessionStorage.setItem("currentGirl", JSON.stringify(girlsCount));
      this.$router.push("/showResultBG");
    }
  }
}
</script>

